import React from 'react';
import FontAwesome from 'react-fontawesome';
import './Card.css';
import {numToIcon} from '../../utils';


const card = ({ icon, clicked, val, turned }) => {
  // console.log(<FontAwesome name={numToIcon[icon]} />);
  return (
  <div value={val} onClick={clicked} className={'Card ' + (turned ? 'Card--Turned' : '')}>
    <div className='Card__Side Card__Side--Front'></div>
    <div className="Card__Side Card__Side--Back">
      <FontAwesome name={numToIcon[icon]} />

    </div>
  </div>
  )
}

export default card;


